import SubscriptionBtn from "./app.subscriptionBtn";
import CancelBtn from "./app.cancelBtn";
import { LoaderFunction } from "@remix-run/node";
import { MONTHLY_PLAN, authenticate, } from "~/shopify.server";
import { useLoaderData } from "@remix-run/react";

export const loader: LoaderFunction = async ({ request }) => {
    const { billing } = await authenticate.admin(request);
    const { hasActivePayment } = await billing.check({
        plans: [MONTHLY_PLAN],
        isTest: process.env.NODE_ENV === 'development',
    });

    // Return the subscription status to the component
    return { isSubscribed: hasActivePayment };
};

type LoaderData = {
    isSubscribed: boolean;
};

